import { useLocation, useNavigate } from 'react-router-dom';
import 'react-datepicker/dist/react-datepicker.css';
import 'assets/css/batteriesAddOrUpdate.css';
import { useRef, useState, useEffect } from 'react';
import {
  updateCompany,
  deleteCompany,
  addCompany,
} from 'app/auth/services/administrator/companies';
import { useDispatch } from 'react-redux';
import { checkPhoneNum, checkEmail } from 'app/utils/validation';
import { DetailTitleBar } from 'app/theme-layouts/mainLayout/components';
import { showMessage } from 'app/store/fuse/messageSlice';
import { open, close } from 'app/store/common/loadingWrap';
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';
import Post from 'app/theme-layouts/mainLayout/components/Post';
import classnames from 'classnames';

function AddOrUpdate() {
  const dispatch = useDispatch();
  const [postFlag, setPostFlag] = useState(false);
  const [address, setAddress] = useState('');
  const [validation, setValidation] = useState([]);

  const saveBtnClick = () => {
    confirmAlert({
      title: `고객사 정보를 ${param.detailLink[0].title} 하시겠습니까?`,
      // message: '메세지 공간입니다.',
      buttons: [
        {
          label: '예',
          onClick: () => {
            const companyObj = {};
            if (ownerPassword.current.value === ownerPasswordCheck.current.value) {
              if (!checkPhoneNum(telNo.current.value)) {
                dispatch(
                  showMessage({ message: '대표전화 형식을 확인해주세요', variant: 'error' })
                );
                return;
              }
              if (!checkEmail(owner.current.value)) {
                dispatch(
                  showMessage({ message: '관리자 ID 이메일 형식을 확인해주세요', variant: 'error' })
                );
                return;
              }
              if (!checkPhoneNum(ownerPhone.current.value)) {
                dispatch(
                  showMessage({ message: '개인 연락처 형식을 확인해주세요', variant: 'error' })
                );
                return;
              }

              companyObj.companyName = companyName.current.value;
              companyObj.companyDesc = companyDesc.current.value;
              companyObj.telNo = telNo.current.value; // 대표전화
              companyObj.address = address;
              companyObj.owner = owner.current.value;
              companyObj.ownerName = ownerName.current.value;
              companyObj.ownerPhone = ownerPhone.current.value;
              companyObj.ownerPassword = ownerPassword.current.value;
              companyObj.useYn = useYn.current.value;
              dispatch(open());
              switch (param.detailLink[0].title) {
                case '추가':
                  addCompany(companyObj)
                    .then((data) => {
                      dispatch(
                        showMessage({
                          message: '고객사 정보가 추가되었습니다.',
                          variant: 'success',
                        })
                      );
                      setValidation([]);
                    })
                    .catch((error) => {
                      showMessage({ message: '고객사 정보 추가 실패.', variant: 'error' });
                      if (error.response.data.code === 400) {
                        const arr = [];
                        for (let i = 0; i < error.response.data.errors.length; i += 1) {
                          arr.push(error.response.data.errors[i]);
                        }
                        setValidation(arr);
                      } else {
                        setValidation([]);
                      }
                    })
                    .finally(() => {
                      dispatch(close());
                    });
                  break;
                case '수정':
                  companyObj.companyId = param.companyId;
                  updateCompany(companyObj)
                    .then((data) => {
                      dispatch(
                        showMessage({
                          message: '고객사 정보가 수정되었습니다.',
                          variant: 'success',
                        })
                      );
                      setValidation([]);
                    })
                    .catch((error) => {
                      dispatch(
                        showMessage({ message: '고객사 정보 수정 실패.', variant: 'error' })
                      );
                      if (error.response.data.code === 400) {
                        const arr = [];
                        for (let i = 0; i < error.response.data.errors.length; i += 1) {
                          arr.push(error.response.data.errors[i]);
                        }
                        setValidation(arr);
                      } else {
                        setValidation([]);
                      }
                    })
                    .finally(() => {
                      dispatch(close());
                    });
                  break;
                default:
              }
            } else {
              dispatch(showMessage({ message: '비밀번호를 확인해주세요', variant: 'info' }));
            }
          },
        },
        {
          label: '취소',
          onClick: () => console.log('no click'),
        },
      ],
    });
  };

  const deleteBtnClick = () => {
    confirmAlert({
      title: `고객사 정보를 삭제 하시겠습니까?`,
      buttons: [
        {
          label: '예',
          onClick: () => {
            dispatch(open());
            deleteCompany(param.companyId)
              .then((data) => {
                dispatch(
                  showMessage({ message: '고객사 정보가 삭제되었습니다.', variant: 'success' })
                );
                navigate('/administrator/companies');
              })
              .catch((error) => {
                dispatch(showMessage({ message: '고객사 정보 삭제 실패.', variant: 'error' }));
                if (error.response.data.code === 409) {
                  dispatch(showMessage({ message: error.response.data.message, variant: 'error' }));
                } else {
                  setValidation([]);
                }
              })
              .finally(() => {
                dispatch(close());
              });
          },
        },
        {
          label: '취소',
        },
      ],
    });
  };

  useEffect(() => {
    param.detailLink[0].title === '수정' && setAddress(param.address);
  }, []);

  const companyName = useRef();
  const companyDesc = useRef();
  const telNo = useRef();
  const owner = useRef();
  const ownerName = useRef();
  const ownerPhone = useRef();
  const ownerPassword = useRef();
  const ownerPasswordCheck = useRef();
  const useYn = useRef();

  const navigate = useNavigate();
  const location = useLocation();
  const param = location.state;

  return (
    <div className="section__grid__wrap content batteries__add__update companies__manage">
      <DetailTitleBar
        flag={param.detailLink[0].title}
        deleteBtnClick={deleteBtnClick}
        saveBtnClick={saveBtnClick}
      />
      {postFlag && (
        <div id="postSearchWrap">
          <Post address={address} setAddress={setAddress} setPostFlag={setPostFlag} />
        </div>
      )}
      {/* input__wrap */}
      <section className="input__wrap">
        <div className="section__inner">
          <ul className="selector__wrap">
            <li>
              <dl>
                <dt>
                  <span className="f__bold" style={{ color: 'red' }}>
                    *&nbsp;
                  </span>
                  회사명
                </dt>
                <dd>
                  <input
                    type="text"
                    name="companyName"
                    defaultValue={param.companyName}
                    className="custom__input"
                    ref={companyName}
                  />
                  {validation.map(
                    (obj) =>
                      obj.type === 'companyName' && (
                        <p className="f__medium field__error">{obj.message}</p>
                      )
                  )}
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>
                  <span className="f__bold" style={{ color: 'red' }}>
                    *&nbsp;
                  </span>
                  대표 전화번호
                </dt>
                <dd>
                  <input
                    ref={telNo}
                    defaultValue={param.telNo}
                    type="text"
                    className="custom__input"
                  />
                </dd>
              </dl>
            </li>
            {/* <li>
              <dl>
                <dt>대표전화</dt>
                <dd>
                  <input
                    type="text"
                    defaultValue={param.telNo}
                    className="custom__input"
                    ref={telNo}
                  />
                </dd>
              </dl>
            </li> */}
            <li className="w100">
              <dl>
                <dt>
                  <span className="f__bold" style={{ color: 'red' }}>
                    *&nbsp;
                  </span>
                  회사주소
                </dt>
                <dd className="address__search__list">
                  <input
                    defaultValue={address}
                    type="text"
                    className="custom__input readonly"
                    readOnly
                  />
                  <button
                    className="custom__btn"
                    onClick={() => {
                      setPostFlag(!postFlag);
                    }}
                  >
                    <svg fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth="2"
                        d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
                      />
                    </svg>
                    <span className="f__medium">검색</span>
                  </button>
                </dd>
              </dl>
            </li>
            <li className="w50">
              <dl>
                <dt>
                  <span className="f__bold" style={{ color: 'red' }}>
                    *&nbsp;
                  </span>
                  관리자ID
                </dt>
                <dd>
                  <input
                    defaultValue={param.owner}
                    type="email"
                    className={classnames('custom__input', [
                      { readonly: param.detailLink[0].title === '수정' },
                    ])}
                    readOnly={param.detailLink[0].title === '수정'}
                    ref={owner}
                  />
                  {validation.map(
                    (obj) =>
                      obj.type === 'companyId' && (
                        <p className="f__medium field__error">{obj.message}</p>
                      )
                  )}
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>
                  <span className="f__bold" style={{ color: 'red' }}>
                    *&nbsp;
                  </span>
                  사용자명
                </dt>
                <dd>
                  <input
                    defaultValue={param.ownerName}
                    type="text"
                    className={classnames('custom__input', [
                      { readonly: param.detailLink[0].title === '수정' },
                    ])}
                    readOnly={param.detailLink[0].title === '수정'}
                    ref={ownerName}
                  />
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>
                  <span className="f__bold" style={{ color: 'red' }}>
                    *&nbsp;
                  </span>
                  비밀번호
                </dt>
                <dd>
                  <input type="password" className="custom__input" ref={ownerPassword} />
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>
                  <span className="f__bold" style={{ color: 'red' }}>
                    *&nbsp;
                  </span>
                  비밀번호 확인
                </dt>
                <dd>
                  <input type="password" className="custom__input" ref={ownerPasswordCheck} />
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>
                  <span className="f__bold" style={{ color: 'red' }}>
                    *&nbsp;
                  </span>
                  고객사 관리자 전화번호
                </dt>
                <dd>
                  <input
                    defaultValue={param.ownerPhone}
                    type="text"
                    className={classnames('custom__input', [
                      { readonly: param.detailLink[0].title === '수정' },
                    ])}
                    readOnly={param.detailLink[0].title === '수정'}
                    ref={ownerPhone}
                  />
                </dd>
              </dl>
            </li>
            <li className="w50">
              <dl>
                <dt>
                  <span className="f__bold" style={{ color: 'red' }}>
                    *&nbsp;
                  </span>
                  사용여부
                </dt>
                <dd>
                  <select defaultValue={param.useYn} className="custom__select" ref={useYn}>
                    <option value="1">사용</option>
                    <option value="0">미사용</option>
                  </select>
                </dd>
              </dl>
            </li>
            <li className="w100">
              <dl>
                <dt>설명</dt>
                <dd>
                  <textarea
                    defaultValue={param.companyDesc}
                    className="custom__input"
                    ref={companyDesc}
                  />
                </dd>
              </dl>
            </li>
          </ul>
        </div>
      </section>
      {/* // input__wrap */}
    </div>
  );
}

export default AddOrUpdate;
