import DaumPostcode from 'react-daum-postcode';

const Post = (props) => {
  const { address } = props;
  const { setAddress } = props;
  const { setPostFlag } = props;

  const onCompletePost = (data) => {
    setAddress(data.address);
    setPostFlag(false);
  };

  const testClose = () => {
    setPostFlag(false);
  };

  const postCodeStyle = {
    display: 'block',
    width: '400px',
    height: '400px',
    padding: '7px',
  };

  return (
    <>
      <button
        className="close__btn f__medium"
        onClick={() => {
          testClose();
        }}
      >
        닫기
      </button>
      <DaumPostcode style={postCodeStyle} autoClose onComplete={onCompletePost} />
    </>
  );
};

export default Post;
