import axios from 'axios';
import config from 'app/auth/services/administrator/companies/config';

const getCompanies = (paramObj) => {
  return new Promise((resolve, reject) => {
    axios
      .get(config.getCompanies, {
        params: paramObj,
      })
      .then((response) => {
        if (response.data.code === 200) {
          const data = response.data.response;
          resolve(data);
        } else {
          reject(response);
        }
      })
      .catch((error) => {
        reject(error);
      });
  });
};

